<template>
  <el-container>
    <el-form :model="formData" ref="formData" :rules="rules" lable-width="100px" class="form">
      <div class="form-title">账号登录</div>
      <el-form-item label="账号" prop="username">
        <el-input type="text" v-model="formData.username" placeholder="请输入账号"></el-input>
      </el-form-item>

      <el-form-item label="密码" prop="password">
        <el-input type="password" v-model="formData.password" placeholder="请输入密码"></el-input>
      </el-form-item>
      <div class="selectTip"><router-link to="/">忘记密码？</router-link></div>
      <el-form-item>
        <el-button type="primary" @click="submitForm('formData')">登录</el-button>
      </el-form-item>
      <div class="registerLink">
        <span> 新用户？</span>
        <router-link to="/Login/register">注册账号 </router-link>
      </div>
      <div class="line">
        <span>登录遇到问题?</span>
      </div>
      <div class="otherIssues">
        <router-link to="/">常见问题</router-link>
        <router-link to="/">联系客服 </router-link>
      </div>
    </el-form>
  </el-container>
</template>

<script>
export default {
  name: 'LoginView',
  data() {
    return {
      formData: {
        username: '',
        password: '',
      },
      rules: {
        username: [
          { required: true, message: '请输入账号', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在3到10个字符之间', tigger: 'blur' },
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 20, message: '密码不能少于6个字符', tigger: 'blur' },
        ],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$message({
            showClose: true,
            message: '验证成功',
            type: 'success',
          });
        } else {
          this.$message({
            showClose: true,
            message: '验证失败',
            type: 'error',
          });
        }
      });
    },
  },
};
</script>

